module.exports = "/*** Saved recipe page ***/\r\n.savedRecipes {\r\n  min-height: calc(100vh - 60px);\r\n  width: 100%;\r\n  overflow: hidden;\r\n}\r\n.wrapper {\r\n  padding-top: 40px;\r\n  width: 1350px;\r\n  margin: auto;\r\n}\r\n.wrapper h1 {\r\n  font-size: 50px;\r\n  margin-bottom: 10px;\r\n}\r\n.titleButtons {\r\n  display: flex;\r\n  flex-direction: row;\r\n  justify-content: space-between;\r\n}\r\n.titleButtons h1 {\r\n  cursor: pointer;\r\n  transition: all 0.2s ease-in-out;\r\n}\r\n.deselected {\r\n  transition: all 0.2s ease-in-out;\r\n  color: rgb(165, 165, 165);\r\n}\r\n/*** Card for the table ***/\r\n.recipeCard {\r\n  /* background-color: #fff; */\r\n  /* box-shadow: 0 3px 15px rgba(0, 0, 0, 0.1); */\r\n  border-radius: 25px;\r\n  padding: 20px;\r\n  width: 70%;\r\n  margin: 20px auto;\r\n}\r\n/*** Search bar for the table ***/\r\n.searchBar {\r\n  width: 90%;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  margin-bottom: 10px;\r\n  position: relative;\r\n  margin: auto;\r\n}\r\n.searchBar input {\r\n  background-color: rgb(241, 241, 241);\r\n  border-radius: 8px;\r\n  width: 100%;\r\n  padding: 14px 20px;\r\n  box-sizing: border-box;\r\n  font-size: 16px;\r\n  border: transparent 2px solid;\r\n  transition: all 0.2s ease-in-out;\r\n}\r\n.searchBar input:focus {\r\n  border: #92b1ff 2px solid;\r\n  transition: all 0.2s ease-in-out;\r\n}\r\n.searchBar i {\r\n  color: #888888;\r\n  position: absolute;\r\n  right: 0;\r\n  padding-right: 20px;\r\n  cursor: pointer;\r\n  transition: all 0.2s ease-in-out 0s;\r\n  visibility: visible;\r\n  opacity: 0;\r\n  font-size: 18px;\r\n}\r\n.visible {\r\n  opacity: 1 !important;\r\n  transition: all 0.2s ease-in-out 0s;\r\n}\r\n/*** Table spacing ***/\r\n.tableWrapper {\r\n  height: 100%;\r\n  width: 945px;\r\n  overflow-x: hidden;\r\n  display: flex;\r\n  scroll-behavior: smooth;\r\n  flex-direction: row;\r\n  align-items: flex-start;\r\n}\r\n.tableWrapper::-webkit-scrollbar {\r\n  width: 0 !important;\r\n}\r\n.tableArea {\r\n  height: 100%;\r\n  min-width: 945px;\r\n}\r\n/*** Recipe table ***/\r\n.recipe-table {\r\n  min-width: 945px;\r\n  display: inline-block;\r\n  font-family: inherit;\r\n  margin: auto;\r\n  background-color: rgba(255, 255, 255, 0);\r\n}\r\nth,\r\ntd {\r\n  vertical-align: middle;\r\n  transition: all 0.2s ease-in-out;\r\n  width: 945px;\r\n}\r\nth {\r\n  font-size: 13px;\r\n}\r\ntd {\r\n  font-size: 15px;\r\n}\r\n.elementRow:hover {\r\n  background-color: rgba(0, 0, 0, 0.1);\r\n  transition: all 0.2s ease-in-out;\r\n  cursor: pointer;\r\n}\r\n.recipeColumn {\r\n  width: 50%;\r\n}\r\n.ellipColumn {\r\n  text-align: right;\r\n  color: rgba(255, 255, 255, 0);\r\n}\r\n.elementRow:hover .ellipColumn {\r\n  color: #000;\r\n}\r\n.endColumn {\r\n  text-align: right;\r\n  width: 22%;\r\n}\r\n.endColumn i {\r\n  padding-left: 5px;\r\n}\r\n/*** Table interaction ***/\r\n.remove {\r\n  font-size: 20px;\r\n  display: flex;\r\n  justify-content: center;\r\n  width: 30px;\r\n  color: #e42063;\r\n}\r\n.remove:hover .fa-heart,\r\n.remove .fa-times {\r\n  display: none;\r\n}\r\n.remove:hover .fa-trash-alt,\r\n.remove .fa-times {\r\n  display: none;\r\n}\r\n.remove:hover .fa-times {\r\n  display: inline;\r\n}\r\n/*** General elements ***/\r\n.breakLine {\r\n  width: 100%;\r\n  height: 2px;\r\n  background-color: #eaeaea;\r\n  border-radius: 25px;\r\n}\r\n/*** No recipe added ***/\r\n.noRecipe {\r\n  text-align: center;\r\n  padding-top: 80px;\r\n  font-size: 30px;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"